import React from 'react';
import { Drawer, Form, Button, Col, Row, Input, Switch } from 'antd';
import axios from 'axios';

import { userApi } from '../../config';

const DrawerForm = (props) => {
  const { visible, onClose, headers } = props;
  const formRef = React.createRef();

  const onSubmit = async (data) => {
    try {
      const res = await axios.post(userApi.create, data, headers);
      console.log("res", res);
      this.formRef.current.resetFields()
      onClose();
    } catch {

    }
  }

  const handleClose = () => {
    formRef.current.resetFields();
    onClose();
  }

  return (
    <div>
      <Drawer
        title="Add New User"
        width={'25%'}
        onClose={handleClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
      >
        <Form layout="vertical"
          hideRequiredMark
          onFinish={onSubmit}
          ref={formRef}
          initialValues={{
            isAdmin: false,
            notification: false,
          }}
        // onFinishFailed={onFinishFailed}
        >
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="name"
                label="Name"
                rules={[{ required: true, message: 'Please enter user name' }]}
              >
                <Input placeholder="Please enter user name" />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                name="email"
                label="Email"
                rules={[{ required: true, type: 'email', message: 'Please enter user email' }]}
              >
                <Input placeholder="Please enter user name" />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item label="Admin" name="isAdmin">
                <Switch />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item label="Notification" name="notification">
                <Switch />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Button
                onClick={handleClose}
                htmlType='reset'
                style={{ marginRight: 8 }}
              >
                Cancel
              </Button>
              <Button
                type="primary"
                htmlType='submit'
              >
                Submit
              </Button>
            </Col>
          </Row>
        </Form>
      </Drawer>
    </div>
  );
}

export default DrawerForm;