import React from 'react';
import axios from 'axios';
import { Spin, Empty, Button, Collapse, Popconfirm } from 'antd';
import { QuestionCircleOutlined } from '@ant-design/icons';

import { UserContext } from '../../contexts';
import { UseContext } from '../../hoc';
import { userApi } from '../../config';
import { alternatorlist as cms } from '../../cms';
import AddUserDrawer from './AddUserDrawer';

const { Panel } = Collapse;

class AdminDashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      error: null,
      data: [],
      addFormVisible: false,
    }
  }

  async componentDidMount() {
    try {
      const { getAuthHeader } = this.props;
      const { data } = await axios.get(userApi.list, getAuthHeader());
      this.setState({ loading: false, data });
    } catch (err) {
      this.setState({ loading: false, error: err })
    }
  }

  toggleAddUserForm = addFormVisible => this.setState({ addFormVisible });

  resetUserPassword = async id => {
    try {
      const { getAuthHeader } = this.props;
      const { data } = await axios.put(`${userApi.resetPassword}/${id}`, {}, getAuthHeader());
      // this.setState({ loading: false, data });
    } catch (err) {
      this.setState({ loading: false, error: err })
    }
  } 

  render() {
    const { loading, error, data, addFormVisible } = this.state;
    const { getAuthHeader } = this.props;
    console.log("::::", loading, error, data)
    if (loading) {
      return (
        <div className="example" style={{ position: 'absolute', left: '50%', top: '50%' }}>
          <Spin size="large" />
        </div>
      )
    }

    if (data.length === 0) {
      return (
        <>
          <Empty>
            <Button onClick={() => this.toggleAddUserForm(true)}>{cms.addAlternatorButton}</Button>
          </Empty>
          <AddUserDrawer headers={getAuthHeader()} visible={addFormVisible} onClose={() => this.toggleAddUserForm(false)} />
        </>
      )
    }
    return (
      <>
        <div>
          <Collapse>

            {data.map(d => (
              <Panel key={d._id} header={d.name} extra={(
                <>
                  <Popconfirm
                    title={`Are you sure you want to Reset Password for ${d.name}?`}
                    icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
                    okText="Yes"
                    cancelText="No"
                    placement="left"
                    onConfirm={() => this.resetUserPassword(d._id)}
                  >
                    <Button>RESET PASSWORD</Button>
                  </Popconfirm>
                  <Popconfirm
                    title="Are you sure you want to delete this user?"
                    icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
                    okText="Yes"
                    placement="left"
                    cancelText="No"
                  >
                    <Button style={{ color: 'red' }} >DELETE</Button>
                  </Popconfirm>
                </>
              )}>
              </Panel>
            ))}
          </Collapse>
        </div>
        <AddUserDrawer visible={addFormVisible} />
      </>
    )
  }
}

export default UseContext(UserContext)(AdminDashboard);